.sidebar {
  width: 260px;
  min-width: 260px;
  background: linear-gradient(180deg, #1e293b 0%, #0f172a 100%);
  color: white;
  padding: 20px 16px;
  display: flex;
  flex-direction: column;
  height: 100vh;
  position: sticky;
  top: 0;
}

.sidebar-header {
  margin-bottom: 24px;
}

.back-btn {
  width: 100%;
  padding: 12px 16px;
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  color: #94a3b8;
  border-radius: 10px;
  font-size: 14px;
  cursor: pointer;
  text-align: left;
  transition: all 0.2s;
}

.back-btn:hover {
  background: rgba(255, 255, 255, 0.1);
  color: white;
}

.sidebar-label {
  font-size: 11px;
  color: #64748b;
  margin-bottom: 12px;
  padding-left: 12px;
  letter-spacing: 1.5px;
  font-weight: 600;
}

.sidebar-sections {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 4px;
  overflow-y: auto;
}

.sidebar-section {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 12px;
  border-radius: 10px;
  transition: all 0.2s;
}

.sidebar-section:hover {
  background: rgba(255, 255, 255, 0.05);
}

.sidebar-section.active {
  background: #3b82f6;
}

.section-main {
  display: flex;
  align-items: center;
  gap: 12px;
  flex: 1;
  cursor: pointer;
  overflow: hidden;
}

.section-num {
  width: 26px;
  height: 26px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  font-weight: 600;
  flex-shrink: 0;
}

.sidebar-section.active .section-num {
  background: rgba(255, 255, 255, 0.2);
}

.section-name {
  font-size: 14px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.section-arrows {
  display: flex;
  flex-direction: column;
  gap: 2px;
  opacity: 0;
  transition: opacity 0.2s;
}

.sidebar-section:hover .section-arrows {
  opacity: 1;
}

.section-arrows button {
  background: rgba(255, 255, 255, 0.1);
  border: none;
  color: white;
  padding: 2px 6px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 10px;
  transition: all 0.15s;
}

.section-arrows button:hover:not(:disabled) {
  background: rgba(255, 255, 255, 0.2);
}

.section-arrows button:disabled {
  opacity: 0.3;
  cursor: not-allowed;
}

.add-section-btn {
  margin-top: 16px;
  padding: 14px;
  background: transparent;
  border: 2px dashed rgba(255, 255, 255, 0.2);
  color: #94a3b8;
  border-radius: 10px;
  font-size: 14px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
}

.add-section-btn:hover {
  border-color: #3b82f6;
  color: white;
  background: rgba(59, 130, 246, 0.1);
}

@media (max-width: 768px) {
  .sidebar {
    position: fixed;
    left: 0;
    top: 0;
    z-index: 200;
    box-shadow: 4px 0 20px rgba(0, 0, 0, 0.3);
  }
}